import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from "./home-component/home-component.component";
import { EmployeesComponent } from "./employees-component/employees-component.component";
import { PositionsComponent } from "./positions-component/positions-component.component";
import { PositionComponent } from "../app/position/position.component";
import { PageNotFoundComponent } from "./page-not-found-component/page-not-found-component.component";
import { EmployeeComponent } from "../app/employee/employee.component";

const routes: Routes = [
{ path: 'home', component: HomeComponent },
{ path: 'employees', component: EmployeesComponent },
{ path: 'positions', component: PositionsComponent },
//added these
{ path: 'employee/:id', component: EmployeeComponent },
{ path: 'position/:id', component: PositionComponent },
{ path: '', redirectTo: "/home", pathMatch: 'full' },
{ path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: []
})
export class AppRoutingModule { }
