import { Component, OnInit } from '@angular/core';
import { EmployeeRaw } from '../data/EmployeeRaw';
import { EmployeeService } from '../data/employee.service';
import { Employee } from '../data/employee';
import { ActivatedRoute } from '@angular/router';
import { PositionService } from '../data/position.service';
import { Position } from '../data/position';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit {

  private paramSubscription: any;
  private employeeSubscription: any;
  private getPositionsSubcription: any;
  private saveEmployeeSubscription: any;
  employee: EmployeeRaw;
  private positions: Position[];
  private successMessage: boolean = false;
  private failMessage: boolean = false;

  constructor(private employeeService: EmployeeService, private router: ActivatedRoute, private positionService: PositionService) { }
 
  //May need to fix the methods below if the output is incorrect
  ngOnInit() {
    // console.log("entered employee.component ngOnInit()")
    
     this.paramSubscription = this.router.params.subscribe((params) => {
        // console.log(params.id);
      this.employeeSubscription = this.employeeService.getEmployee(params.id).subscribe((emp) => {
       
        this.employee = emp[0];

        this.getPositionsSubcription = this.positionService.getPositions().subscribe(data => {
          this.positions = data;
        });

      });
    });
    // console.log(this.para);
  }

  onSubmit(){
    this.saveEmployeeSubscription = this.employeeService.saveEmployee(this.employee).subscribe(
                 () => { this.successMessage = true;
      setTimeout(() => { this.successMessage = false; }, 2500)},
                 () => { this.failMessage = true;
      setTimeout(() => { this.failMessage = false; }, 2500);})
  }

  ngOnDestroy(){
    if(this.paramSubscription != null) {this.paramSubscription.unsubscribe();}
    if(this.employeeSubscription != null) {this.employeeSubscription.unsubscribe();}
    if(this.getPositionsSubcription != null) {this.getPositionsSubcription.unsubscribe();}
    if(this.saveEmployeeSubscription != null) {this.saveEmployeeSubscription.unsubscribe();}

  }
}
