import { Injectable } from '@angular/core';
import { Position } from './position';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PositionService {
private url = 'https://fierce-ridge-73424.herokuapp.com';
  constructor(private http: HttpClient) { }
  //makes a get request using the HTTPClient
  getPositions(): Observable<Position[]>{
  //  console.log("in the getPositions function")
    return this.http.get<Position[]>(this.url + "/positions");
  }
  //savePosition() methods
  savePosition(position: Position): Observable<any>{
    console.log("savePosition()")
  //  console.log("****** " + this.url + "/position", position._id + " ****")
    return this.http.put<any>(this.url + "/position/" + position._id, position);
  }

  //getPosition() method
  getPosition(id): Observable<Position[]>{
    // console.log(this.url + "/position/" + id)
    return this.http.get<Position[]>(this.url + "/position/" + id);
  }
}
