import { Component, OnInit } from '@angular/core';
import { EmployeeService } from '../data/employee.service';
// import { EmployeeComponent } from '../employee/employee.component';
import { Employee } from '../data/employee';
import { EmployeeComponent } from '../employee/employee.component';
import { EmployeeRaw } from '../data/EmployeeRaw';
import { Router } from "@angular/router";


@Component({
  selector: 'app-employees',
  templateUrl: './employees-component.component.html',
  styleUrls: ['./employees-component.component.css']
})
export class EmployeesComponent implements OnInit {

  private employees: Employee[]; 
  private filteredEmployees: Employee[];
  private getEmployeesSub;
  private loadingError: boolean = false;
  // private url = 'https://fierce-ridge-73424.herokuapp.com/';

  constructor(private emp: EmployeeService, private router: Router) {
   }

  ngOnInit() {
    this.getEmployeesSub = this.emp.getEmployees().subscribe(
      employees =>  { this.employees = employees;
      this.filteredEmployees = employees; },
      function (e) {
        this.loadingError = true;
      }
    );
  }

  ngOnDestroy(){
    if(this.getEmployeesSub != 'undefined'){
      this.getEmployeesSub.unsubscribe();
    }
  }

  routeEmployee(id: string) {
    console.log("in routEmployee")
    this.router.navigate(['/employee', id]);
  }

 onEmployeeSearchKeyUP(event:any) {
    let string = event.target.value.toLowerCase();
    // console.log(string);
    this.filteredEmployees = this.employees.filter((employee) => 
    ((employee.FirstName.toLowerCase().indexOf(string) !== -1) || 
    (employee.LastName.toLowerCase().indexOf(string) !== -1) || 
    (employee.Position["PositionName"].toLowerCase().indexOf(string) !== -1)))
 }

}
